import React,{Component} from 'react'
import { hashHistory} from 'react-router';
import { Form, Input, Button, Radio, Spin, message, DatePicker } from 'antd';
const FormItem = Form.Item;
const { TextArea } = Input;
const RadioGroup = Radio.Group;
const { RangePicker } = DatePicker;
import moment from 'moment';
import UploadImg from "./uploadImg.jsx"
export class DataForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            disabled: props.type == "detail" ? true : false,
            title: "",
            img: {
                file: {},
                fileList: []
            },
            "limitNum": "",
            "time": {
                "start": "",
                "end": ""
            },
            "deadlineTime": "",
            "active": 1,
            "address": "",
            "invited": "",
            "contact": "",
            "cost": 0,
            loading: true
        };
        ['checkTime', 'checkImg', 'handleSubmit', 'resetForm', 'showLoading', 'hideLoading'].forEach(m=>this[m] = this[m].bind(this))
    }

    showLoading() {
        this.setState({loading: true});
    }

    hideLoading() {
        this.setState({loading: false});
    }
    componentDidMount() {
        let that = this;
        if(this.props.type == "add") {
            this.setState({loading: false});
            that.KE = K.create('#content', {
                allowFileManager : true,
                afterChange: function() {
                    that.props.form.setFieldsValue({"content" :this.html()});
                }
            });
        } else {
            ajax({
                url: "/activity/getInfoDetail",
                data: {
                    id: this.props.activityID
                },
                callback: function(data) {
                    if(data.status == 200) {
                        let info = data.data.detailInfo;
                        that.setState({
                            loading: false,
                            title: info.title,
                            img: {
                                file: {},
                                fileList: [{
                                    uid: -1,
                                    name: info.img.split("/").pop(),
                                    status: "done",
                                    url: info.img
                                }]
                            },
                            time: {
                                start: moment(info.startTime, 'YYYY-MM-DD HH:mm'),
                                end: moment(info.endTime, 'YYYY-MM-DD HH:mm')
                            },
                            deadlineTime: moment(info.deadlineTime, 'YYYY-MM-DD HH:mm'),
                            address: info.address,
                            invited: info.invited,
                            contact: info.contact,
                            limitNum: info.limitNum,
                            cost: info.cost,
                            content: info.content
                        });
                        that.KE = K.create('#content', {
                            allowFileManager : true,
                            afterChange: function() {
                                that.props.form.setFieldsValue({"content" :this.html()});
                            }
                        });
                        that.KE.html(that.state.content);
                        if(that.state.disabled) {
                            that.KE.readonly();
                        }
                        if(that.state.disabled) {
                            $(".anticon-delete").remove();
                        }
                    } else {
                        message.error(data.errMsg, 1);
                    }
                }
            });
        }
    }

    handleSubmit(e) {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                if(this.props.type == "edit") {
                    values = Object.assign(values, {id: this.props.noticeID});
                }
                values.img = values.img.fileList[0].response ? values.img.fileList[0].response.data.imgUrl : values.img.fileList[0].url;
                values.startTime = values.time[0].format("YYYY-MM-DD HH:mm");
                values.endTime = values.time[1].format("YYYY-MM-DD HH:mm");
                values.deadlineTime = values.deadlineTime.format("YYYY-MM-DD HH:mm");
                ajax({
                    url: "/activity/updateInfo",
                    data: values,
                    callback: function(data) {
                        if(data.status == 200) {
                            message.success("设置成功!", 1, function() {
                                hashHistory.push("/activity")
                            });
                        } else {
                            message.error(data.errMsg, 1)
                        }
                    }
                });
            }
        });
    }

    returnList() {
        hashHistory.push("/activity");
    }

    resetForm() {
        this.setState({
            img: {
                fileList: []
            }
        });
        this.props.form.resetFields();
        this.KE.html(this.props.form.getFieldValue("content"))
    }

    checkImg(rule, value, callback) {
        if(value.fileList.length > 0 && value.fileList[0].status == "done") {
            callback();
            return;
        }
        callback("请选择图片!");
    }

    checkTime(rule, value, callback) {
        if(value.length == 2) {
            callback();
            return;
        }
        callback("请设置活动起止时间!");
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        const formItemLayout = {
            labelCol: { span: 6 },
            wrapperCol: { span: 10 }
        };
        return (
            <Spin spinning={this.state.loading} tip="Loading...">
                <Form>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="标题"
                        hasFeedback
                    >
                        {getFieldDecorator('title', {
                            rules: [
                                { required: true, message: '请输入活动标题!' }
                            ],
                            initialValue: this.state.title
                        })(
                            <Input disabled={this.state.disabled} type="text" />
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="图片"
                        hasFeedback
                    >
                        {getFieldDecorator('img', {
                            rules: [{
                                validator: this.checkImg
                            }],
                            initialValue: this.state.img
                        })(
                            <UploadImg showLoading={this.showLoading} hideLoading={this.hideLoading} />
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="活动起止时间"
                        hasFeedback
                    >
                        {getFieldDecorator('time', {
                            rules: [{validator: this.checkTime}],
                            initialValue: [this.state.time.start, this.state.time.end]
                        })(
                            <RangePicker style={{ width: '100%' }} disabled={this.state.disabled} showTime={{ format: 'HH:mm'}} placeholder={["开始时间", "结束时间"]} format="YYYY-MM-DD HH:mm" />
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="报名截止时间"
                        hasFeedback
                    >
                        {getFieldDecorator('deadlineTime', {
                            rules: [{
                                required: true,
                                message: "请设置报名截止日期!"
                            }],
                            initialValue: this.state.deadlineTime
                        })(
                            <DatePicker style={{ width: '100%' }} disabled={this.state.disabled} showTime={{ format: 'HH:mm'}} format="YYYY-MM-DD HH:mm" />
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="活动地址"
                        hasFeedback
                    >
                        {getFieldDecorator('address', {
                            rules: [
                                { required: true, message: '请输入活动地址!' }
                            ],
                            initialValue: this.state.address
                        })(
                            <Input disabled={this.state.disabled} type="text" />
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="邀请嘉宾"
                        hasFeedback
                    >
                        {getFieldDecorator('invited', {
                            rules: [
                                { required: true, message: '请输入邀请嘉宾!' }
                            ],
                            initialValue: this.state.invited
                        })(
                            <Input disabled={this.state.disabled} type="text" />
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="咨询联系"
                        hasFeedback
                    >
                        {getFieldDecorator('contact', {
                            rules: [
                                { required: true, message: '请输入咨询联系!' }
                            ],
                            initialValue: this.state.contact
                        })(
                            <Input disabled={this.state.disabled} type="text" />
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="费用"
                        hasFeedback
                    >
                        {getFieldDecorator('cost', {
                            rules: [
                                { required: true, message: '请输入费用!' }
                            ],
                            initialValue: this.state.cost
                        })(
                            <Input disabled={this.state.disabled} type="text" />
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="人数"
                        hasFeedback
                    >
                        {getFieldDecorator('limitNum', {
                            rules: [
                                { required: true, message: '请输入活动人数!' }
                            ],
                            initialValue: this.state.limitNum
                        })(
                            <Input disabled={this.state.disabled} type="text" />
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="状态"
                        hasFeedback
                    >
                        {getFieldDecorator('active', {
                            initialValue: this.state.active.toString()
                        })(
                            <RadioGroup disabled={this.state.disabled}>
                                <Radio value="1">有效</Radio>
                                <Radio value="0">无效</Radio>
                            </RadioGroup>
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="活动详情"
                        hasFeedback
                    >
                        {getFieldDecorator('content', {
                            initialValue: this.state.content
                        })(
                            <TextArea disabled={this.state.disabled} placeholder="请输入活动详情" autosize={{ minRows: 6 }} />
                        )}
                    </FormItem>
                    <FormItem
                        wrapperCol={{ span: 12, offset: 6 }}
                    >
                        { !this.state.disabled ? <Button type="primary" onClick={this.handleSubmit} style={{"marginRight": "20px"}}>提交</Button> : ""}

                        <Button onClick={this.returnList} style={{"marginRight": "20px"}}>取消</Button>
                        { !this.state.disabled ? <Button type="danger" onClick={this.resetForm}>重置</Button> : ""}
                    </FormItem>
                </Form>
            </Spin>
        )
    }
}

const WrappedDataForm = Form.create({})(DataForm);

module.exports = WrappedDataForm;


